  /* ---------------------------------- */
  /* Reset                              */
  /* ---------------------------------- */

  :root {
      /*colors*/
      --clr-dark: 230 35% 7%;
      --clr-light: 231 77% 90%;
      --clr-white: 0 0% 100%;

      /*font sizes*/

      --fs-900: 9.375rem;
      --fs-800: 6.25rem;
      --fs-700: 3.5rem;
      --fs-600: 2rem;
      --fs-500: 1.75rem;
      --fs-400: 1.125rem;
      --fs-300: 1rem;
      --fs-200: 0.875rem;

      /*font families*/
      --ff-serif: "Bellefair", serif;
      --ff-sans-cond: "Barlow Condensed", sans-serif;
      --ff-sans-normal: "Barlow", sans-serif;

      /*margins*/
      --m-20: 20px;
      --m-10: 10px;
      --m-8: 8px;
      --m-6: 6px;
      --m-4: 4px;

  }


  /* Box sizing*/

  *,
  *::before,
  *::after {
      box-sizing: border-box;
  }


  /* reset margins */

  body,
  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p,
  figure,
  picture {
      margin: 0;
  }

  h1,
  h2,
  h3,
  h4,
  h5,
  h6,
  p {
      font-weight: 400;
  }



  /* setup body */
  body {
      font-size: var(--fs-400);
      color: hsl(var(--clr-dark));
      background-color: hsl(var(--clr-white));
      line-height: 1.5;
      min-height: 100vh;
      font-family: var(--ff-sans-normal);
  }


  /* to make imaages work easier */
  img,
  picture {
      max-width: 100%;
      display: block;
  }

  /* to make working with forms easier */
  input,
  button,
  textarea,
  select {
      font: inherit;
  }

  /* remove animations for people who turned them off */
  @media (prefers-reduced-motion: reduce) {

      *,
      *::before,
      *::after {
          animation-duration: 0.01ms !important;
          animation-iteration-count: 1 !important;
          transition-duration: 0.01ms !important;
          scroll-behavior: auto !important;
      }
  }

  /* utility classes */

  .flex {
      display: flex;
      gap: var(--gap, 1rem);
  }

  .grid {
      display: grid;
      gap: var(--gap, 1rem);
  }

  /* * + * is looking for a sibling, if the element has a sibling on top, adds the margin, otherwise does not*/
  /* instead of double asterisks, *:not(:first-child) could be used  */
  /* *:where(:not:(:first-child)) also works but not *:is(:not(:first-child)) */
  .flow>*+* {
      margin-top: var(--flow-space, 1rem);
  }

  
  .container {
      padding-inline: 2em;
      margin-inline: auto;
      max-width: 80rem;
    }
    @media (max-width: 768px) {
        .container {
            padding-inline: 1em;
            max-width: 90rem;
        }
    }

  /*screen reader only*/
  .sr-only {
      position: absolute;
      width: 1px;
      height: 1px;
      padding: 0;
      margin: -1px;
      overflow: hidden;
      clip: rect(0, 0, 0, 0);
      white-space: nowrap;
      border: 0;
  }

  /* color */

  .bg-dark {
      background-color: hsl(var(--clr-dark));
  }

  .bg-accent {
      background-color: hsl(var(--clr-light));
  }

  .bg-light {
      background-color: hsl(var(--clr-white));
  }

  .text-dark {
      color: hsl(var(--clr-dark));
  }

  .text-accent {
      color: hsl(var(--clr-light));
  }

  .text-light {
      color: hsl(var(--clr-white));
  }

  /**/

  /*typography*/

  .ff-serif {
      font-family: var(--ff-serif);
  }

  .ff-sans-cond {
      font-family: var(--ff-sans-cond);
  }

  .ff-sans-normal {
      font-family: var(--ff-sans-normal);
  }

  .letter-spacing-1 {
      letter-spacing: 4.75px;
  }

  .letter-spacing-2 {
      letter-spacing: 2.7px;
  }

  .letter-spacing-3 {
      letter-spacing: 2.35px;
  }

  .uppercase {
      text-transform: uppercase;
  }

  .fs-900 {
      font-size: var(--fs-900);
  }

  .fs-800 {
      font-size: var(--fs-800);
  }

  .fs-700 {
      font-size: var(--fs-700);
  }

  .fs-600 {
      font-size: var(--fs-600);
  }

  .fs-500 {
      font-size: var(--fs-500);
  }

  .fs-400 {
      font-size: var(--fs-400);
  }

  .fs-300 {
      font-size: var(--fs-300);
  }

  .fs-200 {
      font-size: var(--fs-200);
  }


  .fs-900,
  .fs-800,
  .fs-700,
  .fs-600 {
      line-height: 1.1;
  }

  .m20 {
      margin-bottom: var(--m-20);
  }

  .m10 {
      margin-bottom: var(--m-10);
  }

  .m8 {
      margin-bottom: var(--m-8);
  }

  .m6 {
      margin-bottom: var(--m-6);
  }

  .m4 {
      margin-bottom: var(--m-4);
  }


  .numbered-title {
      font-family: var(--ff-sans-cond);
      font-size: var(--fs-500);
      text-transform: uppercase;
      letter-spacing: 4.72px;
  }

  .numbered-title span {
      margin-right: .5em;
      font-weight: 700;
      color: hsl(var(--clr-white) / .25)
  }

  .flex-center {
      justify-content: center;
      align-items: center;
      flex-direction: var(--flex-direction, row);
  }


  /*components */

  .large-button {
      position: relative;
      display: grid;
      place-items: center;
      z-index: 1;
      padding: 0 2em;
      aspect-ratio: 1;
      text-decoration: none;
      border-radius: 50%;
  }

  .large-button::after {
      content: '';
      position: absolute;
      z-index: -1;
      width: 100%;
      height: 100%;
      border-radius: 50%;
      background-color: hsl(var(--clr-white) / .15);
      opacity: 0;
      transition: opacity 500ms linear, transform 750ms ease-in-out;
  }

  .large-button:hover::after,
  .large-button:focus::after {
      transform: scale(1.5);
      opacity: 1;
  }


  .primary-navigation {
      --gap: 8rem;
      --underline-gap: 2rem;
      list-style: none;
      padding: 0;
      margin: 0;
      background-color: hsl(var(--clr-white) / .1)
  }

  .primary-navigation a>span {
      margin-right: .5em;
      font-weight: 700;
  }

  .primary-navigation a {
      text-decoration: none;
  }

  .underline-indicators>* {
      cursor: pointer;
      display: grid;
      place-content: center;
      padding: var(--underline-gap, 1rem) 0;
      border: 0;
      border-bottom: 0.2rem solid hsl(var(--clr-white) / 0);
  }

  .underline-indicators>*:hover,
  .underline-indicators>*:focus {
      border-bottom: 0.2rem solid hsl(var(--clr-white) / .25);
  }

  .underline-indicators>.active {
      border-bottom: 0.2rem solid hsl(var(--clr-white) / 1);
  }

  .underline-indicators>[aria-selected="true"] {
      color: 0.2rem solid hsl(var(--clr-white) / 1);
      border-bottom: 0.2rem solid hsl(var(--clr-white) / 1);
  }

  .tab-list {
      --gap: 2rem;
  }

  .dot-indicators>* {
      cursor: pointer;
      border: 0;
      border-radius: 50%;
      aspect-ratio: 1;
      padding: 0.5rem;
      background-color: hsl(var(--clr-white) / .25);
  }

  .dot-indicators>*:hover,
  .dot-indicators>*:focus {
      background-color: hsl(var(--clr-white) / .5);
  }

  .dot-indicators>[aria-selected="true"] {
      background-color: hsl(var(--clr-white));
  }

  .numbered-indicators>* {
      cursor: pointer;
      display: grid;
      place-items: center;
      border: .1rem solid hsl(var(--clr-white) / .25);
      border-radius: 50%;
      aspect-ratio: 1;
      padding: 0rem 2rem;
      background-color: hsl(var(--clr-white) / 0);
      color: hsl(var(--clr-light));
  }

  .numbered-indicators>*:hover,
  .numbered-indicators>*:focus {
      border: .1rem solid hsl(var(--clr-white));
  }

  .numbered-indicators>[aria-selected="true"] {
      background-color: hsl(var(--clr-white));
      border: .1rem solid hsl(var(--clr-white));
      color: hsl(var(--clr-dark));
  }